* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: gray;
}
button {
  cursor: pointer;
}
html {
  background-color: #f3f3f3;
  height: fit-content;
  font-family: sans-serif;
  width: 100%;
  height: 100%;
  position: relative;
}

.App {
  background-color: #f3f3f3;
  width: 400px;
  padding: 10px;
  margin-left: 50px;
}

.backdrop {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.637);
}

.heading {
  margin-bottom: 50px;
}
.card {
  background-color: #fff;
  padding: 30px;
  margin-bottom: 10px;
  border-radius: 10px;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.095);
}

.card-btn {
  color: azure;
  border: 0;
  margin-left: 70%;
  margin-top: 30px;
  background-color: #fcb8ab;
  border-radius: 5px;
  padding: 10px 30px;
}

.modal {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
}
